$color1: rgba(57, 131, 206, 1);
$color2: rgba(255, 255, 255, 1);
$color3: #3e6b98;
$color4: rgba(0, 0, 0, 1);
$color5: rgba(57, 61, 63, 1);
$color6: #1b2a38;


// Variables
@import "variables";

// Custom style
@import "navbar.scss";
@import "margin_and_padding.scss";
@import "medium_editor.scss";

body {
	font-family: 'Muli', sans-serif !important;
}
a:hover {
	color: $color3 !important;
}

// disabled blue outline all input
textarea:hover, 
input:hover, 
textarea:active, 
input:active, 
textarea:focus, 
input:focus,
button:focus,
button:active,
button:hover,
label:focus,
.btn:active,
.btn.active,
.editable:focus
{
    outline:0px !important;
    -webkit-appearance:none !important;
}